<template>
  <div class="tieziItem">
		<!-- start-->
    <tieziitemHeader :items='items'></tieziitemHeader>
		<!-- end-->
    
		<!-- start-->
		<div class='reply' v-if='items.length != 0'>
			<div class='reply-title'></div>
			<div class='bottom-line'></div>
			<div v-for='(item,index) in items.comments' :key='item.id' class='reply-items'>
				<div class='reply-item-title'><!---->
					<img class='reply-item-head' :src='item.avatar'>
					<p class='reply-item-name'>{{item.nickname}}</p>
				</div>
				
				<p @click.stop='reply(item.nickname,item.id)' class='reply-item-content'>{{item.content}}</p><!---->
				<div class='reply-item-img'>
					<img 
            v-for='(src,index) in item.pic_urls' 
            :src='src' 
            @click.stop='lookimg(index,item.pic_urls)' 
            :key='index' 
            class='reply-item-img-item'
          >
				</div>
        
				<!---->
				<div 
          class='reply-item-reply' 
          v-for='(replied,index) in item.comments' 
          @click.stop='reply(replied.nickname,item.id)' 
          :key='reply.id'
        >
					<span class='reply-item-reply-name'>{{replied.nickname}}:</span>
          <span class='reply-item-reply-content'>{{replied.content}}</span>
				</div>
				<div class='reply-item-tool'><!---->
					<!-- <img src='public/quanzi/my/praise.png' class='reply-item-tool-img'> -->
					<img src='public/quanzi/my/comments.png' @click.stop='reply(item.nickname,item.id)' class='reply-item-tool-img'>
					<p class='reply-item-date'>{{item.create_time}}</p>
				</div>
			</div>
		</div>
		<!-- end-->
    
    <!--  -->
		<tieziitemReturn 
      :post_id='post_id' 
      :comment_id='comment_id'
      :prompt='prompt'
      :postflag='postflag'
      @getdata='login(post_id)'
      @comments='comments'
    >
    </tieziitemReturn>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui'
import { Toast } from 'mint-ui'
import tieziitemHeader from './tieziitemHeader'
import tieziitemContent from './tieziitemContent'
import tieziitemReturn from './tieziitemReturn'
export default {
  name: 'tieziItem',
  components:{
    tieziitemHeader:tieziitemHeader,
    tieziitemContent:tieziitemContent,
    tieziitemReturn:tieziitemReturn
  },
  data () {
    return {
			title:'',
			items:{},
			valueText:'',
			comment_id:'',
			images:[],/**/
      address:'',
      prompt:'',
      post_id:'',
      postflag:false
    }
  },
	mounted:function(){
		this.$emit('changetitle',this.$data.title)
	},
	created:function(){
		this.login(this.$route.query.id);
	},
	methods:{
		login:function(e){
      Indicator.open('Loading...');
			this.axios({
				url:'posts/'+e,
				method:'get'
			}).then(re => {
        Indicator.close();
				if(re.data.status != 200){
					Toast(re.data.message)
				}else{
          this.items = re.data.data
          this.post_id = re.data.data.id
          console.log(this.items.content)
        }
				
			})
		},
		reply:function(nickname,comment_id){/**/
			let that = this
			that.comment_id = comment_id
			that.prompt = `${nickname}:`
      this.postflag = true
		},
		lookimg (index,data) {/**/
			let that = this;
			wx.previewImage({
				current:data[index],
				urls: data
			})
		},
    comments (data) {
      console.log(data)
      if(data){
        this.postflag = data;
      }else{
        this.postflag = data;
        this.comment_id = '';
        this.prompt = ``;
      }
      
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .tieziItem{
    margin-top:10vw;
  }
	.reply{
		margin-top:3vw;
		position:relative;
		margin-left:2vw;
		margin-right:2vw;
    margin-bottom:15vw;
	}
	.reply-title{
		font-size:5vw;
		font-weight:700;
		text-align:center;
	}
	.bottom-line{
		text-align:center;
		width:15vw;
		border:1px solid #19b5ff;
		display:inline-block;
		position:absolute;
		left:40vw;
	}
	.reply-items{
		margin-top:2vw;
		background:#f8f8f8;
		padding:1vw;
	}
	.reply-item-title{
		display:flex;
		align-items:center;
	}
	.reply-item-head{
		width:6vw;
		height:6vw;
		border-radius:50%;
	}
	.reply-item-name{
		color:#999;
	}
	.reply-item-content{
		margin-left:6vw;
		word-wrap: break-word;
	}
	.reply-item-img{
		display:flex;
		flex-wrap:wrap;
		flex-direction:row;
		align-items:center;
		margin-left:6vw;
	}
	.reply-item-img-item{
		width:25vw;
		height:25vw;
		margin-right:2vw;
		margin-bottom:2vw;
	}
	.reply-item-tool{
		display:flex;
		flex-direction:row-reverse;
    margin-top:5px;
	}
	.reply-item-tool-img{
		margin-left:2vw;
		width:5vw;
		height:5vw;
	}
	.reply-item-tool-img:first-child{
		margin-right:8vw;
	}
	.reply-item-date{
		color:#999;
		font-size:3vw;
	}
	.reply-item-reply{
		background:#fff;
		margin-left:6vw;
		margin-right:1vw;
	}
	.reply-item-reply-name{
		color:#19b5ff;
	}
	.reply-item-reply-content{
		font-size:3vw;
	}
	.posting{
		background:#fff;
		position:fixed;
		bottom:2vw;
		width:100%;
		display:flex;
		flex-direction:row;
		align-items:center;
		flex-wrap:wrap;
		margin-left:0vw;
		border:1px solid #eee;
	}
  .posting-item{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding:2vw 3vw;
  }
  .postingtext{
    flex:1;
    border:none;
    background:#fff;
  }
	.weui-cell{
		width: 60vw;
	}
	.posting-img{
		width:6vw;
		height:6vw;
		margin-right:2vw;
	}
  .posting-btn{
    font-size:4.5vw;
  }
	.chooseimg{
		width:98vw;
		margin-left:2vw;
		margin-right:2vw;
		display:flex;
		flex-wrap:wrap;
		flex-direction:row;
	}
	.chooseimg-item{
		width:30vw;
		height:30vw;
		margin-right:2vw;
		margin-bottom:2vw;
	}
</style>



