import Vue from 'vue'
import Router from 'vue-router'
import tiezi from '@/components/tiezi/tiezi'/**/
import tieziitem from '@/components/tieziitem/tieziitem'/**/
import my from '@/components/my/my'/**/
import message from '@/components/message'/**/
import mytiezi from '@/components/mytiezi/mytiezi'/**/
import collection from '@/components/collection'/**/
import praise from '@/components/praise'/**/
import myReturnCard from '@/components/myReturnCard/myReturnCard'/**/
import posting from '@/components/posting'/**/
import postingvoice from '@/components/postingvoice'/**/
import topicList from '@/components/topicList/topicList'/**/
import search from '@/components/search/search'/**/

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'tiezi',
      component: tiezi
    }, {
      path: '/my',
      name: 'my',
      component: my
    }, {
      path: '/message',
      name: 'message',
      component: message
    }, {
      path: '/mytiezi',
      name: 'mytiezi',
      component: mytiezi
    }, {
      path: '/collection',
      name: 'collection',
      component: collection
    }, {
      path: '/praise',
      name: 'praise',
      component: praise
    }, {
      path: '/myReturnCard',
      name: 'myReturnCard',
      component: myReturnCard
    }, {
      path: '/posting',
      name: 'posting',
      component: posting
    }, {
      path: '/postingvoice',
      name: 'postingvoice',
      component: postingvoice
    }, {
      path: '/tieziitem',
      name: 'tieziitem',
      component: tieziitem
    }, {
      path: '/topicList',
      name: 'topicList',
      component: topicList
    }, {
      path: '/search',
      name: 'search',
      component: search
    }
  ]
})



