<template>
  <div class="menu">
    <!--  -->
    <div class="classificate">
        <div class="classificate-item" v-for="(arr,index) in array" @click.stop='go(index)'>
          <img class='classificate-img' :src="arr.pic_url" alt>
          <p class="classificate-name">{{arr.name}}</p>
        </div>
      
    </div>
    <!--  -->
  </div>
</template>

<script>
export default {
  name: "tieziMenu",
  data() {
    return {
      array: [
      ]
    }
  },
  created(){
    this.getData()
  },
  methods: {
    getData(){
      this.axios({
        url:'keywords'
      }).then(res => {
        if(res.data.status == 200){
          this.array = res.data.data
        }
      })
    },
    go (index) {
      let name = this.array[index].name
      let id = this.array[index].id
      this.$router.push({name:'topicList',query:{id:id,name:name}})
    }
  }
};
</script>


<style lang='scss' scoped>
/*  */
.classificate{
  background: #fff;
  display: block;
  margin-bottom: 3vw;
  overflow:hidden;
  overflow-x:auto;
  white-space: nowrap;
  height:18vw;
  .classificate-item{
    display:inline-block;
    width: 20vw;
    text-align: center;
    overflow:hidden;
    padding:1vw 0;
    margin: 2vw 1vw;
    .classificate-img{
      width: 8vw;
      height: 8vw;
    }
    .classificate-name{
      font-size: 3vw;
      color: #858585;
    }
  }
}
/*  */
</style>



