<template>
  <div class="praise">
		<div class='collection' v-for='(item,index) in items' @click.stop='jieshu(item.source_id,index)'  :key='index'>
			<div class='content'>
				<img v-if='item.pic_urls.length != 0' :src='item.pic_urls[0]' class='content-img'>
				<div class='content-item'>
					<div class='content-item-title'>
            <span class='content-item-key'># {{item.keywords}} #</span>{{item.title}}
          </div>
					<div class='content-item-content' v-html='item.content'></div>
				</div>
			</div>
			<div class='source'>
				<div>{{item.create_time}}</div>
				<div>{{item.sex == 1 ? "  ":"  "}}</div>
				<div>{{item.nickname}}</div>
				<img>
				<div>:</div>
			</div>
		</div>
		<wv-actionsheet type="ios" :actions="actions" cancel-text="" v-model="sheetVisible"/>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui';
import { Toast } from 'mint-ui';
export default {
  name: 'praise',
  data () {
    return {
			title:'',
			items:[],
			startDate:'',
			endDate:'',
			sheetVisible:false,
			target_id:'',
			target_index:'',
			actions:[
				{
					name: '',
					method: this.remove
				},
				{
					name: '',
					method: this.go
				}
			],
			index:1,
			flag:true
    }
  },
	created:async function(){
		let that = this;
		that.items = await that.getlist();
		window.addEventListener('scroll', this.handleScroll);
	},
	mounted:function(){
		this.$emit('changetitle',this.$data.title)
	},
  beforeDestroy(){
    window.removeEventListener('scroll', this.handleScroll);
    console.log('')
  },
	methods:{
		getlist:async function(){
			let that = this;
			return await new Promise((resolve,reject) =>{
        Indicator.open('Loading...');
				that.axios({
					url:'mylikes',
					method:'get',
					params:{
						page:that.index
					}
				}).then(function(re){
          Indicator.close();
					if(re.data.status == 200){
						that.flag = true;
						resolve(re.data.data);
					}else if(re.data.status == 204){
            Toast('')
          }
				})
			})
		},
		remove:function(){
			let that = this;
      let data = {
        source_id: that.target_id,
        status:2,
        type:1
      }
			that.axios({
				url:'likes',
				method:'post',
				data:this.qs.stringify(data)
			}).then(function(re){
				console.log(re)
				if(re.data.status == 200){
					that.items.splice(that.target_index,1);
				}
			})
		},
		jieshu:function(id,index){
			let that = this;
			console.log(id,index);
			that.sheetVisible = true;
			that.target_id = id;
			that.target_index = index;
			
		},
		go:function(e){
			let that = this;
			that.$router.push({name: 'tieziitem',query:{id:that.target_id}});
		},
		handleScroll:async function(){
			let that = this;
			if(document.body.scrollHeight <= scrollY+window.screen.height && that.flag){
				that.flag = false;
				that.index+=1;
				console.log('',that.items)
				that.items.push(...await that.getlist());
				console.log('',that.items)
			}
		}
	}
}
</script>

<style scoped>
.praise{
	margin-bottom:55px;
	margin-top:12vw;
}
.collection{
	background:#fff;
	padding:2vw;
	border-bottom:1px solid #eee;
	margin:1vw 4vw;
}
.content{
	display:flex;
	align-items:flex-start;
  overflow: hidden;
}
.content-img{
	width: 15vw;
  height: 15vw;
	margin-right:1vw;
}
.content-item{
  flex:1;
  padding-right:1vw;
  overflow:hidden;
}
.content-item-title{
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	font-weight:700;
}
.content-item-key{
  color:#19b5ff;
  font-size:3vw;
  padding:0.5vw;
}
.content-item-content{
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}
.source{
	display:flex;
	flex-direction: row-reverse;
	margin-top:2vw;
}
</style>



