<template>
  <div class="mytiezi">
		<div v-for='(item,index) in items'  @click.stop='jieshu(item.id,index)' :key='index' class='item'>
			<!--start-->
			<div class='title'>
				<img class='title-img' :src='item.avatar'>
				<div class='title-card'>
					<div class='title-card-item'>
						<div class='title-card-name'>{{item.nickname}}</div>
						<div :class='item.sex == 1 ? "title-card-man":"title-card-woman"'>{{item.sex == 1 ? '':''}}</div>
					</div>
					<div class='title-card-item'>
						<div class='title-card-data'>{{item.create_time}}</div>
					</div>
				</div>
			</div>
			<!--end-->
			<!--start-->
			<div class='content'>
				<div class='content-title'>
          <span class='content-key'># {{item.keyowrs_name}} #</span>{{item.title}}
        </div>
				<div class='content-content'>{{item.content}}</div>
				<div class='content-img'>
					<img v-if='index < 3' v-for='(src,index) in item.pic_urls' :src='src' key='index' class='content-img-item'>
				</div>
				<div class='content-place'>
					<img src='public/quanzi/tieba/data/place.png' class='content-place-img'>
					<div class='content-place-content'>{{item.address}}</div>
				</div>
			</div>
			<!--end-->
			<!--start-->
			<div class='bottom'>
				<div class='bottom-item'>
          <img class='bottom-item-img' src='public/quanzi/tieba/data/readed.png'>{{item.hits_count}}
				</div>
				<div class='bottom-item'>
          <img class='bottom-item-img' src='public/quanzi/tieba/data/talk.png'>{{item.comment_count}}
				</div>
				<div class='bottom-item'>
          <img class='bottom-item-img' :src='item.islike == 1 ? "public/quanzi/tieba/data/praise.png":"public/quanzi/tieba/data/unpraise.png"'>{{item.like_count}}
				</div>
			</div>
			<!--end-->
		</div>
		<wv-actionsheet type="ios" :actions="actions" cancel-text="" v-model="sheetVisible"/>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui';
import { Toast } from 'mint-ui';
export default {
  name: 'mytiezi',
  data () {
    return {
			title:'',
			items:[],
			sheetVisible:false,
			target:'',
			index:1,
			actions:[
				{
					name: '',
					method: this.remove
				},
				{
					name: '',
					method: this.go
				}
			],
			flag:true
    }
  },
	created:async function(e){
		let that = this;
		that.items = await that.getlist();
		window.addEventListener('scroll', this.handleScroll);
	},
  beforeDestroy(){
    window.removeEventListener('scroll', this.handleScroll);
    console.log('')
  },
	mounted:function(){
		console.log(this)
		this.$emit('changetitle',this.$data.title)
	},
	methods:{
		getlist:async function(){//
			let that = this;
			return await new Promise((resolve,reject) =>{
        Indicator.open('Loading...');
				that.axios({
					url: 'myposts',
					method:'GET',
					params:{
						page:that.index
					}
				}).then(function(re){
          Indicator.close();
					if(re.data.status == 200){
						that.flag = true;
						resolve(re.data.data);
					}else if (re.data.status == 204){
            Toast('')
          }
					
				})
			})
		},
		remove:function(){//
			let that = this;
			that.axios({
				url:'myposts/' + that.target.id,
				method:'delete'
			}).then(function(re){
				if(re.data.status==200){
					that.items.splice(that.target.index,1)
				}
			})
		},
		jieshu:function(id,index){//
			let that = this;
			that.sheetVisible = true;
			that.target = {id:id,index:index};
		},
		go:function(e){//
			let that = this;
			that.$router.push({name: 'tieziitem',query:{id:that.target.id}});
		},
		handleScroll:async function(){//
			let that = this;
			if(document.body.scrollHeight <= scrollY+window.screen.height && that.flag == true){
				that.flag = false;
				that.index+=1;
				console.log('',that.items)
				that.items.push(...await that.getlist());
				console.log('',that.items)
			}
		}
	}
}
</script>


<style scoped>
.mytiezi{
  margin-top:12vw;
}
.item{
	background:#fff;
	margin-top:2vw;
	padding-left:2vw;
  padding-right: 2vw;
}
.title{
	display:flex;
	align-items:center;
}
.title-img{
	width:10vw;
	height:10vw;
	border-radius:50%;
  margin-right:1vw;
}
.title-card-item{
	display:flex;
	direction:row;
}
.title-card-man{
	color:#19b5ff;
	margin-left:2vw;
}
.title-card-woman{
	color:#ffb0d2;
	margin-left:2vw;
}
.title-card-data{
	font-size:3vw;
	color:#555;
}
.content-title{
	font-weight:700;
}
.content-key{
  color:#19b5ff;
  font-size:3vw;
  padding:0.5vw;
}
.content-content{
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}
.content-img{
	display:flex;
	align-items:center;
}
.content-img-item{
	width:30vw;
	height:30vw;
	margin-right:1vw;
	margin-bottom:1vw;
}
.content-place{
	display:flex;
	align-items:center;
}
.content-place-img{
	width:3vw;
	height:4vw;
  margin-right:1vw;
}
.content-place-content{
	font-size:3vw;
	color:#555;
}
.bottom{
	display:flex;
	justify-content:center;
	align-items:center;
	border-top:1px solid #eee;
	margin-top:1vw;
	padding-top:1vw;
	padding-bottom:1vw;
}
.bottom-item{
	flex-grow:1;
	display:flex;
	align-items:center;
	justify-content:center;
}
.bottom-item-img{
	width:4vw;
	height:4vw;
  margin-right:1vw;
}
</style>



