<template>
  <div id="app" v-if='flag'>
		<wv-header class='wx-header' v-if='title != ""' :title="title"   background-color='rgb(33, 150, 243)'>
			<div class="btn-back" slot="left" v-show='backflag'>
				<img src='/public/quanzi/header/back.png' class="header-back" @click="back" />
			</div>
		</wv-header>
		<div class="line"></div>
    
    <router-view @changetitle="changetitle" :msg="msg"/>
		
    <div v-if='title == "" || title == ""' class='footer'>
      <div v-for='(item,index) in menus' class='footer-item' @click.stop='toaddress(item.url,index)'>
        <img class='footer-img' :src='menuFlag == index ? item.chooseimgurl : item.imgurl' />
        <div>{{item.name}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
	data () {
    return {
			title:'',
			msg:'',
			index:false,
			tieba:false,
			my:false,
			backflag:true,
			flag:true,
			axiosflag:true,
      menuFlag:0,
      menus:[
        {name:'',url:'tiezi',imgurl:'public/quanzi/.png',chooseimgurl:'public/quanzi/.png'},
        {name:'',url:'my',imgurl:'public/quanzi/.png',chooseimgurl:'public/quanzi/.png'}
      ]
    }
  },
	created:function(){
		let that = this;
		that.geturl();
		console.log(this.$route)
	},
	methods:{
		back:function(){
			this.$router.go(-1);
		},
    toaddress(url,index){
      this.menuFlag = index;
      this.$router.push({name:url});
    },
		changetitle(msg) {
				this.title = msg;
				this.my = false;
				this.index = false;
				this.tieba = false;
        this.backflag = false;
				if(msg==''){
					this.my = true;
					return false;
				}else if(msg==''){
					this.index = true;
					return false;
				}else if(msg==''){
					this.tieba = true;
					return false;
				}
				this.backflag = true;
    },
		geturl:function(){//jwt
			let that = this;
			let flag = '';
      if(sessionStorage.jwt != undefined){
        flag = sessionStorage.jwt;
      }else{
        flag = this.$route.query.token;
        sessionStorage.jwt = flag;
      }
			that.flag = true;
      this.axios.defaults.headers.common['Access-Token'] = flag;  //axios,
      this.axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8';  //axios,
      that.getconfig();
		},
		getconfig:function(){//
			let that = this;
			that.axios({
				url:'wxConfig',
				method:'get'
			}).then(function(re){
				wx.config({
						debug: false, // ,apialertpclogpc
						appId: re.data.data.appId, // 
						timestamp: re.data.data.timestamp, // 
						nonceStr: re.data.data.nonceStr, // 
						signature: re.data.data.signature,// 
						jsApiList: ['chooseWXPay','getLocation','getLocalImgData','startRecord','stopRecord','onVoiceRecordEnd','playVoice','stopVoice','chooseImage','uploadImage','uploadVoice'] // JS
				});
			})
		}
		
	}
}
</script>

<style lang='scss'>
.wx-header{
  height:10vw !important;
  font-size:3vw !important;
}
.wv-header-title{
  font-size:4.5vw !important;
}
.btn-back{
  height:100%;
  display:flex;
  align-items:center;
  position:absolute;
  top:0;
}
.header-back{
  width:8vw;
  height:8vw;
}
html{
	background:#eee;
}
div,p,span{
	-webkit-user-select: none;
  user-select: none;
}
*{
	font-size:4vw;
	padding:0;
	margin:0;
}

.weui-tabbar{
	height:30px;
}

.footer{
  height:50px;
  position:fixed;
  bottom:0;
  left:0;
  width:100vw;
  display:flex;
  justify-content:center;
  align-items:center;
  background-color:#fff;
  box-shadow: 0 -1px 1px #eee;
 
  .footer-item{
    text-align:center;
    flex:1;
    display:flex;
    justify-content:center;
    align-items:center;
    flex-flow:column wrap;
    
    .footer-img{
      width:5vw;
      height:5vw;
    }
    
  }
  
}

</style>



