<template>
  <div class="hello">
		<!-- start-->
    <tieziitemHeader></tieziitemHeader>
		<div class='lz'>
			<div class='lz-title'>{{items.title}}</div><!---->
			<div class='lz-card'><!---->
				<img :src='items.avatar' class='lz-card-img'>
				<div >
					<div>{{items.nickname}} <span :class='items.sex == 1 ? "lz-card-man":"lz-card-woman"'>{{items.sex == 1 ? "":""}}</span></div>
					<div>{{items.create_time}}</div>
				</div>
			</div>
			
			<div><!---->
				<p class='content-text'>{{items.content}}</p>
				<div class='lz-content-img'>
					<img v-for='(src,index) in items.pic_urls' :src='src' @click.stop='lookimg(index,items.pic_urls)' :key='index' class='lz-content-img-item'>
				</div>
			</div>
			
			<div class='lz-like_count'>{{items.like_count}}</div>
		</div>
		<!-- end-->
		<!-- start-->
		<div class='reply'>
			<div class='reply-title'></div>
			<div class='bottom-line'></div>
			<div v-for='(item,index) in items.comments' :key='item.id' class='reply-items'>
				<div class='reply-item-title'><!---->
					<img class='reply-item-head' :src='item.avatar'>
					<p class='reply-item-name'>{{item.nickname}}</p>
				</div>
				
				<p class='reply-item-content'>{{item.content}}</p><!---->
				<div class='reply-item-img'>
					<img v-for='(src,index) in item.pic_urls' :src='src' @click.stop='lookimg(index,item.pic_urls)' :key='index' class='reply-item-img-item'>
				</div>
				<!---->
				<div class='reply-item-reply' v-for='(reply,index) in item.comments' :key='reply.id'>
					<span class='reply-item-reply-name'>{{reply.nickname}}</span><span class='reply-item-reply-content'>:{{reply.content}}</span>
				</div>
				<div class='reply-item-tool'><!---->
					<img src='static/images/my/praise.png' class='reply-item-tool-img'>
					<img src='static/images/my/comments.png' @click.stop='reply(item.nickname,item.id)' class='reply-item-tool-img'>
					<p class='reply-item-date'>{{item.create_time}}</p>
				</div>
			</div>
		</div>
		<!-- end-->
		
		<!-- start-->
		<div class='posting'>
			<wv-input placeholder="" v-model="valueText"></wv-input>
			<img src='static/images/posting/image.png' v-if='comment_id.length == 0' @click.stop='chooseimg()' class='posting-img'>
			<img src='static/images/tieba/post.png' @click.stop='posting(items.id)' class='posting-img'>
			<div class='chooseimg'>
				<img v-for='(url,index) in images' @click.stop='lookimg(index,images)' :src='url' :key='index' class='chooseimg-item'>
			</div>
		</div>
		<!-- end-->
  </div>
</template>

<script>
import tieziitemHeader from './tieziitemHeader'
export default {
  name: 'collection',
  components:{
    tieziitemHeader:tieziitemHeader
  },
  data () {
    return {
			title:'',
			items:[],
			valueText:'',
			comment_id:'',
			images:[]/**/
    }
  },
	mounted:function(){
		console.log(this)
		this.$emit('changetitle',this.$data.title)
	},
	created:function(){
		let that = this;
		let id = '';
		if(that.$route.params.id == undefined){
			id = localStorage.getItem("router")
		}else{
			id = that.$route.params.id;
			localStorage.setItem("router",id)
		}
		
		that.login(id);
	},
	methods:{
		login:function(e){
			let that = this;
			that.axios({
				url:'posts/'+e,
				method:'get'
			}).then(function(re){
				if(re.data.status != 200){
					that.nologin(e)
					return false;
				}
				that.items = re.data.data;
			})
		},
		nologin:function(e){
			let that = this;
			that.axios({
				url:'forums/'+e,
				method:'get',
				params:{
					key:0
				}
			}).then(function(re){
				if(re.data.status != 200){
					that.$toast.fail('');
					return false;
				}
				that.items = re.data.data;
			})
		},
		reply:function(nickname,comment_id){/**/
			let that = this;
			that.comment_id = comment_id;
			that.valueText = '@'+nickname;
		},
		posting:async function(post_id){/**/
			let that = this;
			let data = [];
			if(that.images.length != 0){
				for(let i in that.images){
					data.push(await that.upimage(that.images[i]));
				}
			}
			if(that.valueText.length == 0 && data.length == 0){
				that.$toast.text({
					duration: 1000,
					message: ''
				});
				return false;
			}
			let params = new URLSearchParams();
			params.append('post_id',post_id);
			params.append('content',that.valueText);
			params.append('address','');
			if(that.comment_id != ''){
				params.append('comment_id',that.comment_id);
			}
			that.axios({
				url:'comments',
				method:'post',
				data:params
			}).then(function(re){
				var images = new URLSearchParams();
				images.append('img',data);
				images.append('mold','comment');
				if(that.images.length != 0){
					that.axios({
						url:'wxFile/' + re.data.data,
						method:'post',
						data:images
					}).then(function(re){
						console.log(re)
					})
				}
				if(re.data.status == 200){
					that.login(that.$route.params.id);
					that.valueText = '';
					that.comment_id = '';
					that.images = '';
				}
			})
		},
		lookimg:function(index,data){/**/
			let that = this;
			wx.previewImage({
				current:data[index],
				urls: data
			})
		},
		chooseimg:function(e){/**/
			let that = this;
			let nums = 9 - that.images.length;
			if(nums <= 0 ){
				that.$toast.text({
					duration: 1000,
					message: '9'
				});
				return false;
			}
			wx.chooseImage({
				count:nums,
				sizeType: ['original'], // 
				sourceType: ['album', 'camera'], // 
				success: function (res) {
					that.images.push(...res.localIds);
				}
			});
			wx.error(function(re){
				console.log('',that.images);
			})
		},
		upimage:async function(images){/**/
			return await new Promise((resolve,reject) =>{
				wx.uploadImage({
					localId:images,
					isShowProgressTips: 1,
					success:function(res){
						resolve(res.serverId);
					},
					fail:function(res){
					}
				})
			});
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.hello{
		background:#fff;
	}
	.lz{
		margin-left:2vw;
		margin-right:2vw;
	}
	.lz-title{
		font-weight:700;
		font-size:5vw;
	}
	.lz-card{
		display:flex;
		align-items:center;
	}
	.lz-card-img{
		border-radius:50%;
		width:10vw;
		height:10vw;
	}
	.content-text{
		width:100%;
		word-wrap: break-word;
	}
	.lz-card-man{
		color:#19b5ff;
	}
	.lz-card-woman{
		color:#ffb0d2;
	}
	.lz-content-img{
		display:flex;
		align-items:center;
		flex-wrap:wrap;
	}
	.lz-content-img-item{
		width:30vw;
		height:30vw;
		margin-right: 2vw;
    margin-bottom: 2vw;
	}
	.lz-like_count{
		width:8vw;
		background:#19b5ff;
		text-align:center;
		font-size:3vw;
		color:#fff;
	}
	.reply{
		margin-top:3vw;
		position:relative;
		margin-left:2vw;
		margin-right:2vw;
	}
	.reply-title{
		font-size:5vw;
		font-weight:700;
		text-align:center;
	}
	.bottom-line{
		text-align:center;
		width:15vw;
		border:1px solid #19b5ff;
		display:inline-block;
		position:absolute;
		left:40vw;
	}
	.reply-items{
		margin-top:2vw;
		background:#f8f8f8;
		padding:1vw;
	}
	.reply-item-title{
		display:flex;
		align-items:center;
	}
	.reply-item-head{
		width:6vw;
		height:6vw;
		border-radius:50%;
	}
	.reply-item-name{
		color:#999;
	}
	.reply-item-content{
		margin-left:6vw;
		word-wrap: break-word;
	}
	.reply-item-img{
		display:flex;
		flex-wrap:wrap;
		flex-direction:row;
		align-items:center;
		margin-left:6vw;
	}
	.reply-item-img-item{
		width:25vw;
		height:25vw;
		margin-right:2vw;
		margin-bottom:2vw;
	}
	.reply-item-tool{
		display:flex;
		flex-direction:row-reverse;
	}
	.reply-item-tool-img{
		margin-left:2vw;
		width:5vw;
		height:5vw;
	}
	.reply-item-tool-img:first-child{
		margin-right:8vw;
	}
	.reply-item-date{
		color:#999;
		font-size:3vw;
	}
	.reply-item-reply{
		background:#fff;
		margin-left:6vw;
		margin-right:1vw;
	}
	.reply-item-reply-name{
		color:#19b5ff;
	}
	.reply-item-reply-content{
		font-size:3vw;
	}
	.posting{
		background:#fff;
		position:fixed;
		bottom:4vh;;
		width:100%;
		display:flex;
		flex-direction:row;
		align-items:center;
		flex-wrap:wrap;
		margin-left:0vw;
		border:1px solid #eee;
	}
	.weui-cell{
		width: 60vw;
	}
	.posting-img{
		width:8vw;
		height:8vw;
		margin-left:3vw;
	}
	.chooseimg{
		width:98vw;
		margin-left:2vw;
		margin-right:2vw;
		display:flex;
		flex-wrap:wrap;
		flex-direction:row;
	}
	.chooseimg-item{
		width:30vw;
		height:30vw;
		margin-right:2vw;
		margin-bottom:2vw;
	}
</style>



