<template>
  <div class="hello">
		<tieziheader></tieziheader>
		<tieziMenu></tieziMenu>
		<!-- <message></message> -->
		<top></top>
		<tieziData></tieziData>
  </div>
</template>

<script>
import tieziData from './tieziData'
import tieziheader from './header'
import tieziMenu from './menu'
import message from './message'
import top from './top'
export default {
  name: 'mytiezi',
  components:{
    tieziheader:tieziheader,
    tieziData:tieziData,
    tieziMenu:tieziMenu,
    message:message,
    top:top,
  },
  data () {
    return {
			title:'',
    }
  },
	mounted:function(){
		this.$emit('changetitle',this.$data.title)
	},
	methods:{
		
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>

</style>



