<template>
  <div class="searchInput">
		<input 
      class='input' 
      type='text' 
      v-model='searchData' 
      autofocus="autofocus" 
      placeholder=''
    />
    <div class='input-btn' @click.stop='search'></div>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui';
import { Toast } from 'mint-ui';
export default {
  name: 'search',
  data () {
    return {
			searchData:''
    }
  },
	created () {
	},
	methods: {
    search () {
      let data = {
        str:this.searchData
      }
      console.log(data);
      this.$emit('getdata',data)
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.searchInput{
  display:flex;
  justify-content:center;
  align-items:center;
  padding:5vw 10vw;
  background:#fff;
	.input{
    border:1px solid #777;
    background:none;
    outline:none;
    padding:2vw 4vw;
    border-radius:5vw;
    flex:1;
  }
  .input-btn{
    padding:1vw 2vw;
    border:1px solid #777;
    color:#777;
    border-radius:1vw;
    font-size:3.5vw;
    margin-left:3vw;
  }
}
</style>



