<template>
  <div class="returnCard">
    <div class='mask' v-if='postflag' @click.stop='hide'></div>
		<div class='posting'>
			<div class='posting-item'>
        <!---->
        <input
          class='postingtext'
          @click.stop='show'
          :placeholder='prompt'
          v-model='valueText'
          ref='input'
        />
        
        <!---->
        <img
          src='public/quanzi/tieziitem/.png'
          v-if='comment_id.length == 0'
          @click.stop='chooseimg'
          class='posting-img'
        />
        <div class='posting-btn' @click.stop='posting(items.id)'></div>
      </div>
			<div class='chooseimg' v-if='images.length != 0'>
				<img
          v-for='(url,index) in images'
          @click.stop='lookimg(index,images)'
          :src='url'
          :key='index'
          class='chooseimg-item'
        />
			</div>
		</div>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui'
import { Toast } from 'mint-ui'
export default {
  name: 'returnCard',
  props:{
    comment_id:String,
    post_id:String,
    prompt:String,
    postflag:Boolean
  },
  data () {
    return {
			title:'',
			items:[],
			valueText:'',
			images:[],/**/
      address:''
    }
  },
  watch:{
    comment_id (val) {
      if(val != ''){
        this.$refs.input.focus();
      }
    }
  },
	methods:{
		lookimg (index,data) {/**/
			let that = this;
			wx.previewImage({
				current:data[index],
				urls: data
			})
		},
    chooseimg (e) {/**/
			let that = this;
			let nums = 9 - that.images.length;
			if(nums <= 0 ){
				Toast('9');
				return false;
			}
			wx.chooseImage({
				count:nums,
				sizeType: ['original'], // 
				sourceType: ['album', 'camera'], // 
				success:res => {
					that.images.push(...res.localIds);
				}
			});
		},
    upimage:async function(images){/**/
			return await new Promise((resolve,reject) =>{
				wx.uploadImage({
					localId:images,
					isShowProgressTips: 1,
					success:function(res){
						resolve(res.serverId);
					},
					fail:function(res){
					}
				})
			});
		},
    posting:async function(){/**/
			let data = [];
			if(this.images.length != 0){
				for(let i in this.images){
					data.push(await this.upimage(this.images[i]));
				}
			}
			if(this.valueText.length == 0 && data.length == 0){
				Toast('')
				return false;
			}
      let params = {
        post_id: this.post_id,
        content:this.valueText,
        address:this.address,
        img:JSON.stringify(data),
        class:1,
        comment_id: this.comment_id
      }
			if(this.comment_id != ''){
				params.comment_id = this.comment_id;
			}
      Indicator.open('Loading...');
			this.axios({
				url:'comments',
				method:'post',
				data:this.qs.stringify(params)
			}).then(re => {
        Indicator.close();
        if(re.data.status == 200){
          Toast('')
          this.$emit('getdata','ok')
        }else{
          Toast(re.data.message)
        }
        this.$emit('comments',false)
        this.valueText = '';
        this.images = [];
			})
		},
    hide () {
      this.$emit('comments',false)
    },
    show () {
      this.$emit('comments',true)
      this.$refs.input.focus();
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
  .mask{
    position:fixed;
    width:100vw;
    height:100vh;
    background:rgba(0,0,0,0.5);
    top:0;
    left:0;
    z-index:10;
  }
	.posting{
		background:#fff;
		position:fixed;
		bottom:2vw;
		width:100%;
		display:flex;
		flex-direction:row;
		align-items:center;
		flex-wrap:wrap;
		margin-left:0vw;
		border:1px solid #eee;
    z-index:11;
    .posting-item{
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      padding:0vw 3vw;
      .postingtext{
        flex:1;
        border:none;
        background:#fff;
        padding:3vw;
      }
      .posting-img{
        width:6vw;
        height:6vw;
        margin-right:2vw;
      }
      .posting-btn{
        font-size:4.5vw;
        padding:0 1vw;
      }
    }
	}
	.chooseimg{
		width:98vw;
		margin-left:2vw;
		margin-right:2vw;
    overflow-x:auto;
    white-space:nowrap;
    height:32vw;
	}
	.chooseimg-item{
		width:30vw;
		height:30vw;
		margin-right:2vw;
		margin-bottom:2vw;
	}
</style>



