<!---->
<template>
  <div class="hello">
		<wv-group><!---->
			<wv-input placeholder="" v-model="valueText"></wv-input>
			<wv-textarea placeholder="" :rows="6" v-model="content1"></wv-textarea>
			<div class='posting-img'><!---->
			<img src='public/quanzi/posting/voice.png' @click.stop='show' class='posting-img-item'>
		</div>
		</wv-group>
		<div class='sound' v-if='sound.length != 0' @click.stop='play'>
			<img :src='soundimg' class='soundimg' >
		</div>
		<wv-button type="primary" @click='posting' :mini="true"></wv-button>
		<div class='record' :class='flag ? "show":""'>
			<div class='outside circle' @click.stop='tap' v-if='flag' :class='css ? "animation":""' >
				<div class='center circle'>
					<div class='inside circle'>
						<div class='luyin'>
							<img class='luyin_image' src='public/quanzi/posting/voice2.png'>
							<p class='font'>{{font}}</p>
						</div>
					</div>
				</div>
			</div>
		</div>
  </div>
</template>

<script>
export default {
  name: 'posting',
  data () {
    return {
			title:'',
			valueText:'',
			content1:'',
			flag:false,
			font:'',
			css:false,
			sound:'',
			soundimg:'public/quanzi/posting/stop.png',
			status:false,
      address:''
    }
  },
	created:function(e){
		this.getground();
	},
	methods:{
		posting:async function(e){
			let that = this;
			if(that.title.length == 0 || that.sound.length ==0){
				that.$toast.text({
					duration: 1000,
					message: ''
				});
				return false;
			}
			let serverId = await that.upvoice(that.sound);
      
      let params = {
        title:this.valueText,
        keywords_id:1,
        content:this.content1,
        class:1,
        type:3,
        address:this.address,
        voice_url:serverId
      }
			that.axios({
				url:'posts',
				method:'post',
				data:this.qs.stringify(params)
			}).then(function(re){
        if(re.data.status == 200){
          that.$router.push({name: 'tiezi'});
        }
			})
		},
    /**
     * 
    **/
		show:function(){
			let that = this;
			that.flag = that.flag ? false:true;
		},
    /**
     * 
    **/
		tap:function(){/**/
			let that = this;
			if(that.css){
				that.css = false;
				that.stoprecord();
			}else{
				that.css = true;
				wx.startRecord();
			}
		},
    /**
     * 
    **/
		stoprecord:function(){
			let that = this;
			wx.stopRecord({
				success: function (res) {
					that.sound = res.localId;
				}
			});
		},
    /**
     * 
    **/
		play:function(){
			let that = this;
			if(this.status == false){
				this.status = true;
				this.soundimg = 'public/images/posting/play.gif';
				wx.playVoice({
					localId: this.sound // IDstopRecord
				});
			}else{
				this.status = false;
				this.soundimg = 'public/images/posting/stop.png';
				wx.stopVoice({
					localId: this.sound // IDstopRecord
				});
			}
			wx.onVoicePlayEnd({
				success:res => {
					this.status == false;
					this.soundimg = 'public/images/posting/stop.png';
				}
			});
		},
    /**
     * 
    **/
		upvoice:async function(images){
			return await new Promise((resolve,reject) =>{
				wx.uploadVoice({
					localId:images,
					isShowProgressTips: 1,
					success:function(res){
						resolve(res.serverId);
					},
					fail:function(res){
					}
				})
			});
		},
    /**
     * 
    **/
		getground:function(){
			let that = this
			wx.getLocation({
				type: 'wgs84',
				success:res => {
					this.axios({
            url:'forumUserLatLng',
            params:{
              longitude:res.longitude,
              latitude:res.latitude
            }
          }).then(res => {
            console.log(res);
            this.address = res.data.data;
          })
				}
			});
		}
	},
	mounted:function(){
		this.$emit('changetitle',this.$data.title)
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
	background:#fff;
  margin-top:10vw;
}
.posting-img-item{
	width:6vw;
	height:6vw;
	margin-left:3vw;
}
.posting-img{
	position:absolute;
	bottom:0vw;
}
.weui-btn{
	left: 84vw;
}
.weui-btn_primary{
	background:#19b5ff;
}
.record{
	position:absolute;
	bottom:50px;
	width:100vw;
	display:flex;
	justify-content:center;
	align-items:center;
}
.circle{
	border-radius:50%;
	display:flex;
	justify-content:center;
	align-items:center;
}
.outside{
	width:50vw;
	height:50vw;
	background:rgba(25, 181, 255, 0.4);
}
.center{
	width:40vw;
	height:40vw;
	background:rgba(25, 181, 255, 0.6);
}
.inside{
	width:30vw;
	height:30vw;
	background:rgba(25, 181, 255, 0.8);
}
.luyin{
	width:28vw;
	height:28vw;
	display:flex;
	justify-content:center;
	flex-direction:column;
	align-items:center;
}
.luyin_image{
	height:12vw;
	width:8vw;
}
.font{
	color:#fff;
	margin-top:2vw;
}

.animation{
  animation: outside 0.8s infinite forwards alternate;
}
@keyframes outside{
  0%{transition:All 0.4s ease-in-out;}
  100%{transform:scale(1.2);}
}
.show{
  animation: show 0.8s  forwards;
}
@keyframes show{
  0%{bottom:-80vw; opacity: 0}
  100%{bottom:50; opacity: 1}
}
.sound{
	width:10vw;
	background:#19b5ff;
	height:7.5vw;
	min-width:20vw;
	max-width:60vw;
	position:absolute;
	margin-top:1vw;
	margin-left:2vw;
	border-radius:1vw;
	display:flex;
	align-items:center;
}
.soundimg{
	width:5vw;
	height:4vw;
	margin-left:2vw;
}
</style>



