<template>
  <div class="myReturnCard">
		<div class='content' v-for='(item,index) in items' @click='go(item.post_id)'>
			<!--start-->
			<div class='title'>
				<img :src='item.avatar' class='title-img'>
				<div class='title-item'>
					<div class='title-item-nickname'>{{item.nickname}}</div>
					<div class='title-item-time'>{{item.create_time}}</div>
				</div>
			</div>
			<!--end-->
			
			<!--start-->
			<div class='mycontent'>
				<div class='mycontent-content'>:{{item.pic_urls.length != 0 ? "[[]]":""}}{{item.content}}</div>
			</div>
			<!--end-->
			<!--start-->
			<div class='lzcontent'>
				<img 
          v-if='item.post_pic_urls.length > 0'
          :src='item.post_pic_urls[0]'
          class='lzcontent-img'
        />
				<div class='lzcontent-item'>
					<p class='lzcontent-item-title'>
            <span class='lzcontent-item-key'># {{item.keywords}} #</span>
            {{item.title}}
          </p>
					<p class='lzcontent-item-content' v-html='item.post_content'></p>
				</div>
			</div>
			<!--end-->
		</div>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui';
import { Toast } from 'mint-ui';
export default {
  name: 'myReturnCard',
  data () {
    return {
			title:'',
			items:[],
			index:1,
			flag:true
    }
  },
	created:async function(){
		let that = this;
		that.items = await that.getlist();
		window.addEventListener('scroll', this.handleScroll);
	},
  beforeDestroy(){
    window.removeEventListener('scroll', this.handleScroll);
  },
	mounted:function(){
		this.$emit('changetitle',this.$data.title)
	},
	methods:{
		go:function(e){//
			this.$router.push({name: 'tieziitem',query:{id:e}});
		},
		getlist:async function(){//
			let that = this;
			return await new Promise((resolve,reject) =>{
        Indicator.open('Loading...');
				that.axios({
					url:'mycomments',
					method:'get',
					params:{
						page:that.index
					}
				}).then(function(re){
          Indicator.close();
					if(re.data.status == 200){
						that.flag = true;
						resolve(re.data.data);
					}else if(re.data.status == 204){
            Toast('')
          }
				}).catch(function(err){
					reject(err)
				})
			})
		},
		handleScroll:async function(){//
			let that = this;
			let y = window.scrollY;
			let h = window.outerHeight;
			let scrollHeight = document.body.scrollHeight;
			if(document.body.scrollHeight <= scrollY+window.screen.height && that.flag == true){
				that.flag = false;
				that.index+=1;
				console.log('',that.items)
				that.items.push(...await that.getlist());
				console.log('',that.items)
			}
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .myReturnCard{
    margin-top:12vw;
  }
	.mycontent-content{
		overflow:hidden;
		margin-right:4vw;
		text-overflow: ellipsis;
	}
	.content{
		padding-left:2vw;
		background:#fff;
		margin-bottom:2vw;
		padding-bottom:2vw;
	}
	.mycontent{
		
	}
	.title{
		display:flex;
		align-items:center;
		margin-top:1vw;
    padding-top:1vw;
	}
	.title-img{
		width:10vw;
		height:10vw;
    margin-right:1vw;
		border-radius:50%;
	}
	.title-item-nickname{
		
	}
	.title-item-time{
		font-size:3.5vw;
		color:#999;
	}
	.lzcontent{
		background:#eee;
		padding-left:2vw;
		display:flex;
		align-items:center;
		margin-right:2vw;
    overflow: hidden;
	}
  .lzcontent-item-key{
    color:#19b5ff;
    font-size:3vw;
    padding:0.5vw;
  }
	.lzcontent-img{
		width:10vw;
		height:10vw;
    margin-right:1vw;
	}
  .lzcontent-item{
    flex:1;
    padding-right:1vw;
    overflow:hidden;
  }
	.lzcontent-item-title{
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		font-weight:700;
	}
	.lzcontent-item-content{
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
    max-height:7vw;
	}
</style>



