<template>
  <div class="information">
		<div class="head">
			<img :src='head_img' class='head_img'>
			<p>{{head_name}}</p>
		</div>
  </div>
</template>

<script>
export default {
  name: 'information',
  data () {
    return {
			head_img:'',
			head_name:''
    }
  },
  created:function(){
		this.getinfo();//
	},
	methods:{
		getinfo:function(){//
			this.axios({
				url:'info',
				method:'get'
			}).then(re => {
        if(re.data.status == 200){
          this.head_img = re.data.data.avatar;
          this.head_name = re.data.data.nickname;
          this.head_signature = re.data.data.nickname;
        }
			})
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.head{
	background:rgb(33, 150, 243);
	color:#fff;
	text-align:center;
	padding-bottom:2vw;
  
  .head_img{
    display:inline-block;
    width:20vw;
    height:20vw;
    border-radius:50%;
    margin-top: 10px;
  }

}

</style>



