<template>
  <div class="topicList">
		<div class='tieziitem' 
      v-for='(item,index) in items' 
      :key='item.id'  
      @click.stop='go(item.id)' 
      @scroll.native="scrollHandler"
    >
			<!--start-->
			<div class='title'>
				<img :src='item.avatar' class='title-image'><!---->
				<div class='title-card'>
					<div class='title-card-item'>
						<div>{{item.nickname}}</div>
						<!--<div v-bind:class='[item.sex == 1 ? "man":"woman"]'>{{item.sex == 1 ? '':''}}</div>-->
						<div 
              v-bind:class='[item.sex == 1 ? "level-man":"level-woman"]'
            >
              {{item.level}}
            </div>
					</div>
					<div class='title-card-time'>{{item.create_time}}</div> 
				</div>
			</div>
      
      <img class='logo-img' v-if='item.is_essence == 1' src='/public/quanzi/tieba/data/jinghua.png'/>
			<!--end-->
      
			<img src='public/quanzi/tieba/data/put.png' class='put' @click.stop='tool(item.id,index)'>
      
			<!-- start-->
			<div class='content'>
				<div class='content-title'>
          <span v-if='item.keyowrs_name != null' class='content-title-keywords'># {{item.keyowrs_name}} #</span>
          {{item.title}}
        </div><!---->
				<div class='content-text' style='-webkit-box-orient:vertical; -webkit-line-clamp:3; ' v-html='item.content'></div><!---->
        
				<div class='content-img'><!---->
					<img class='content-imges' 
            v-for='(img,imgindex) in item.pic_urls' 
            v-if='imgindex < 3'
            :key='imgindex'
            :src='img'
            @click.stop='look(index,imgindex)'
          >
				</div>
        
        <div class='sound'><!---->
          <audio 
            :src="item.voice_url" 
            v-if='item.voice_url.length != 0' 
            controls="controls"
            class='sound-item'
          ></audio>
        </div>
        
        
				<div v-if='item.address.length != 0' class='content-address'>
					<img src='public/quanzi/tieba/data/place.png' class='content-address-img'>
					<p class='content-address-content'>{{item.address}}</p>
				</div>
			</div>
			<!-- end-->
			
			<!-- start-->
			<div class='foot'>
				<div class='foot-item'>
					<img class='foot-item-img' src='public/quanzi/tieba/data/readed.png'>{{item.hits_count}}
				</div>
				<div class='foot-item'>
					<img class='foot-item-img' src='public/quanzi/tieba/data/talk.png'>{{item.comment_count}}
				</div>
				<div class='foot-item' @click.stop='love(item.islike,item.id,index)'>
					<img class='foot-item-img' :src='item.islike == 1 ? "public/quanzi/tieba/data/praise.png":"public/quanzi/tieba/data/unpraise.png"'>{{item.like_count}}
				</div>
			</div>
			<!-- end-->
      
      <!-- start-->
      <div class='like-img' v-if='item.like_user'>
        <img class='like-img-item' v-for='(imgdata,index) in item.like_user' :src='imgdata'/>
      </div>
      <!-- end-->
      
      <!-- start-->
      <div class='comment' v-if='item.comment'>
        <div class='comment-item' v-if='index < 3' v-for='(comment,index) in item.comment'>
          <div class='comment-item-name'>{{comment.nickname}}: </div>
          <div class='comment-item-content'>{{comment.content}}</div>
        </div>
      </div>
      <!-- end-->
		</div>
    
		<!-- start-->
			<img class='posting' @click='posting' src='public/quanzi/tieba/data/posting.png'>
		<!-- end-->
		
		<wv-actionsheet type="ios" :actions="actions" cancel-text="" v-model="sheetVisible"/>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui';
import { Toast } from 'mint-ui';
export default {
  name: 'topicList',
  data () {
    return {
			soundimg: 'public/quanzi/posting/stop.png',
			items: [],
			data: {
				"jwt": sessionStorage.getItem('jwt'),
				"index" : 1
			},
			flag: true,
			target: '',
			sheetVisible: false,
			actions: [
				{
					name:'',
					method: this.comments
				}
			],
			status:false,
      index:'',
      title:''
    }
  },
	created:async function(){
    this.data.index = 1
		this.getpost();
		window.addEventListener('scroll', this.handleScroll);//
    this.$emit('changetitle',this.$route.query.name)
	},
  beforeDestroy(){
    window.removeEventListener('scroll', this.handleScroll);
    console.log('')
  },
	methods:{
    /**
     * 
    **/
		posting:function(e){
			this.$router.push({name: 'posting'});
		},
    /**
     * 
    **/
		go:function(e){
			this.$router.push({name: 'tieziitem',query:{id:e}});
		},
    /**
     * 
    **/
		love: function(islike,id,index){
			if(islike != 1){
        this.praise(islike,index,id,1)
			}else if(islike == 1){
        this.praise(islike,index,id,2)
			}
		},
    /**
     * 
    **/
		praise:function(islike,index,id,like){
			let that = this;
      let data = {
        source_id:id,
        status:like,
        type:1
      }
      that.axios({
        url:'likes',
        method:'post',
        data:that.qs.stringify(data)
      }).then(re => {
        if(re.data.status == 200){
          if(islike == 1){
            this.$set(this.items[index],'islike',2)
            this.$set(this.items[index],'like_count',--this.items[index].like_count)
          }else{
            this.$set(this.items[index],'islike',1)
            this.$set(this.items[index],'like_count',++this.items[index].like_count)
          }
        }
      })
		},
    /**
     * 
    **/
		getpost:function(){
      Indicator.open('Loading...');
			let that = this;
			let page = that.data.index;
      that.axios({
        url: 'posts',
        params:{
          page:that.data.index,
          keywords_id:this.$route.query.id
        },
        method:'get'
      }).then(re => {
        Indicator.close();
        if(re.data.status == 200){
          that.flag = true;
          this.items.push(...re.data.data);
        }else if(re.data.status == 204){
          Toast('')
        }else{
          Toast(re.data.message)
        }
      })
		},
		tool:function(id,index){
			let that = this;
			that.sheetVisible = true;
			that.target = id;
      this.index = index;
      this.actions[0].name = this.items[index].collection == 0 ? "":""
		},
    /**
     * 
    **/
		comments:function(e){
      Indicator.open('Loading...');
      let data = {
        post_id:this.target,
        status:this.items[this.index].collection == 0 ? 1 : 2
      }
			this.axios({
				url:'mycollections',
				method:'post',
				data:this.qs.stringify(data)
			}).then( res => {
        Indicator.close();
        if(res.data.status == 200){
          Toast(this.items[this.index].collection == 0 ? '':'')
          this.$set(
            this.items[this.index],
            'collection',
            this.items[this.index].collection == 0 ? 1 : 0
          )
          
        }else{
          Toast(res.data.status)
        }
				
			})
		},
    /**
     * 
    **/
		handleScroll:async function(){
			let that = this;
			if(document.body.scrollHeight <= scrollY+window.screen.height && that.flag){
				that.flag = false;
				that.data.index = that.data.index + 1;
				that.getpost();
			}
		},
    /**
     * 
    */
    look(index,imgindex){
      wx.previewImage({
        current: this.items[index].pic_urls[imgindex], // http
        urls: this.items[index].pic_urls// http
      });
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.topicList{
	margin-top:11vw;
}
.tieziitem{
  margin-top:2vw;
  background:#fff;
  position:relative;
  overflow:hidden;
  .title{ /****/
    display:flex;
		direction:row;
		align-items:center;
		padding-top:2vw;
		margin-left:2vw;
		margin-bottom:2vw;
    .title-image{ /****/
      width:10vw;
      height:10vw;
      border-radius: 50%;
      margin-right:2vw;
    }
    .title-card{
      .title-card-item{
        display:flex;
        align-items:center;
        .man{
          color:#19b5ff;
          margin-left:1vw;
        }
        .woman{
          color:#ffb0d2;
          margin-left:1vw;
        }
        .level-man{
          border:1px solid #19b5ff;
          padding:0.25vw 3vw;
          font-size:3vw;
          color:#19b5ff;
          margin-left:2vw;
          border-radius:1vw;
        }
        .level-woman{
          border:1px solid #ffb0d2;
          padding:0.25vw 3vw;
          font-size:3vw;
          color:#ffb0d2;
          margin-left:2vw;
          border-radius:1vw;
        }
      }
      .title-card-time{
          font-size:2vw;
      }
    }
  }
  .logo-img{
    width:10vw;
    height:10vw;
    transform: rotate(-45deg);
    position:absolute;
    top:-2vw;
    left:65vw;
  }
  .put{
    position:absolute;
		top:5vw;
		right:6vw;
		width:4vw;
		height:4vw;
		padding:2vw;
  }
  .content{
    margin:0 2vw;
    .content-title{
      font-weight:700;
      white-space: nowrap;
      overflow:hidden;
      text-overflow:ellipsis;
      .content-title-keywords{
        font-weight:400;
        border-radius:1vw;
        color:#19b5ff;
      }
    }
    .content-text{
      width:100%;
      word-wrap: break-word;
      overflow:hidden;
      display:-webkit-box;
      text-overflow:ellipsis;
    }
    .content-img{
      display:flex;
      flex-flow: row wrap;
      justify-content:space-between;
      margin-top:1vw;
      .content-imges{
        width:30vw;
        height:30vw;
        margin-right:1vw;
        margin-bottom:1vw;
      }
      &:after{
        content:'';
        width:30vw;
      }
    }
    .sound{
      margin-top:1vw;
      border-radius:1vw;
      display:flex;
      justify-content:center;
      align-items:center;
      .sound-item{
      }
    }
    .content-address{
      margin-top:1vw;
      margin-bottom:1vw;
      display:flex;
      align-items:center;
      .content-address-img{
        width:2vw;
        height:3vw;
        margin-right:1vw;
      }
      .content-address-content{
        font-size:3vw;
        color:#777;
      }
    }
  }
  .foot{
		margin:0 2vw;
		display:flex;
		direction:row;
		align-items:center;
		border-top:1px solid #eee;
    .foot-item{
      display:flex;
      align-items:center;
      justify-content:center;
      flex-grow:1;
      margin-bottom:1vw;
      margin-top:1vw;
      flex:1;
      .foot-item-img{
        width:4vw;
        height:4vw;
        margin:0 1vw;
      }
    }
	}
  .like-img{
    display:flex;
    justify-content:flex-start;
    align-items:center;
    border-top:1px solid #eee;
    padding:2vw;
    .like-img-item{
      width:9vw;
      border-radius:10vw;
      height:9vw;
      margin-right:-3vw;
      border:1px solid #ccc;
    }
  }
  .comment{
    .comment-item{
      display:flex;
      justify-content:flex-start;
      align-items:center;
      white-space:nowrap;
      padding: 0vw 2vw;
      .comment-item-name{
        font-size:3.5vw;
        color:#777;
        margin-right:1vw;
      }
      .comment-item-content{
        font-size:3.5vw;
      }
    }
  }
}
.posting{
  width:12vw;
  height:12vw;
  position:fixed;
  border-radius:50%;
  bottom:6vw;
  right:2vw;
}
</style>



