<template>
  <div class="Content">
    <div class="content">
      <div class='content-item' v-for='(item,index) in items' :key='index' @click.stop='go(item.url)'>
        <div class='content-item-content'>
          <img :src='item.imgsrc' class='cell-icon'>
          <div>{{item.title}}</div>
        </div>
        <img src='public/quanzi/my/.png' class='go'>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Content',
  data () {
    return {
			items:[
				{url:'mytiezi',imgsrc:'public/quanzi/my/mytiezi.png',title:''},
				{url:'collection',imgsrc:'public/quanzi/my/collection.png',title:''},
				{url:'myReturnCard',imgsrc:'public/quanzi/my/comments.png',title:''},
				{url:'praise',imgsrc:'public/quanzi/my/praise.png',title:''},
				//{url:'set',imgsrc:'public/quanzi/my/set.png',title:''}
			]
    }
  },
	methods:{
    go(url){
      this.$router.push({name:url});
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>

.content{
	margin-top:10px;
  
  .content-item{
    display:flex;
    padding:10px 15px;
    background-color:#ffffff;
    border-bottom:1px solid #eee;
    justify-content:space-between;
    align-items:center;
    
    .content-item-content{
      display:flex;
      align-items:center;
      
      .cell-icon{
        width:5vw;
        height:5vw;
        vertical-align:middle;
        margin-right:2vw;
      }
    }
    
  }
  
}


.go{
  width:4vw;
	height:4vw;
	vertical-align:middle;
}

</style>



