<template>
  <div class="collection">
		<div class='content' v-for='(item,index) in items' :key='item.id' @click.stop='jieshu(item.post_id,index)'>
			<div class='information'>
				<img class='information-img' v-if='item.pic_urls != ""' :src='item.pic_urls[0]'>
				<div class='information-item'>
					<p class='information-title'>
            <span class='information-key'># {{item.keywords}} #</span>{{item.title}}
          </p>
          <p 
            class='information-content-item' 
            style='-webkit-box-orient:vertical; -webkit-line-clamp:2;' 
            v-html='item.content'>
          </p>
				</div>
			</div>
			<div class='source'>
				:&nbsp;
				<img :src='item.avatar' class='source-img'>
				<p>{{item.nickname}}</p>
				<p v-bind:class='[item.sex == 1 ? "source-man":"source-woman"]'>{{item.sex == 1 ? "":""}}</p>
				<p>{{item.create_time}}</p>
			</div>
		</div>
		<wv-actionsheet type="ios" :actions="actions" cancel-text="" v-model="sheetVisible"/>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui';
import { Toast } from 'mint-ui';
export default {
  name: 'collection',
  data () {
    return {
			title:'',
			items:[],
			endDate:'',
			sheetVisible:false,
			target_id:'',
			target_index:'',
			actions:[
				{
					name: '',
					method: this.remove
				},
				{
					name: '',
					method: this.go
				}
			],
			index:1,
			flag:true
    }
  },
	mounted:function(){
		console.log(this)
		this.$emit('changetitle',this.$data.title)
	},
	created:async function(){
		let that = this;
		that.items = await that.getlist();
		window.addEventListener('scroll', this.handleScroll);
	},
  beforeDestroy(){
    window.removeEventListener('scroll', this.handleScroll);
    console.log('')
  },
	methods:{
		go:function(e){//
			let that = this;
			that.$router.push({name: 'tieziitem',query:{id:that.target_id}});
		},
		getlist:async function(){//
			let that = this;
			return await new Promise((resolve,reject) =>{
        Indicator.open('Loading...');
				that.axios({
					url:'mycollections',
					method:'get',
					params:{
						page:that.index
					}
				}).then(function(re){
          Indicator.close();
					if(re.data.status == 200){
						that.flag = true;
						resolve(re.data.data);
					}else{
						reject('')
					}
				})
			})
		},
		remove:function(){//
			let that = this;
      let params = {
        post_id:this.target_id,
        status:2
      }
			that.axios({
				url: 'mycollections',
				method:'POST',
				data:this.qs.stringify(params)
			}).then(re => {
				if(re.data.status == 200){
					this.items.splice(that.target_index,1);
				}
			})
		},
		jieshu:function(id,index){//
			let that = this;
      that.sheetVisible = true;
      that.target_id = id;
      that.target_index = index;
		},
		handleScroll:async function(){//
			let that = this;
			if(document.body.scrollHeight <= scrollY+window.screen.height && that.flag == true){
				that.flag = false;
				that.index+=1;
				that.items.push(...await that.getlist());
			}
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .collection{
    margin-top:12vw;
  }
	.content:first-child{
	}
	.content{
		margin:0vw 2vw 2vw 2vw;
		background:#fff;
		padding-bottom:7vw;
		padding-left:2vw;
	}
	.information{
		display:flex;
		align-items:center;
	}
	.information-img{
		width:10vw;
		height:10vw;
		margin-right:1vw;
	}
	.information-item{
		overflow:hidden; 
    flex:1;
    padding-right:1vw;
	}
  .information-title{
		font-weight:700;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
	}
  .information-key{
    color:#19b5ff;
    font-size:3vw;
    padding:0.5vw;
  }
	.information-content-item{
		overflow:hidden; 
    word-break:break-all;
    text-overflow:clip;
    display:-webkit-box;
    max-height:7vw;
	}
	.source{
		display:flex;
		align-items:center;
		float:right;
		padding-right:2vw;
	}
	.source-img{
		width:4vw;
		height:4vw;
	}
	.source-man{
		color:#19b5ff;
		margin-left:1vw;
		margin-right:1vw;
	}
	.source-woman{
		color:#ffb0d2;
		margin-left:1vw;
		margin-right:1vw;
	}
</style>



