<template>
  <div class="my">
		<information></information>
		<menuList></menuList>
  </div>
</template>

<script>
import information from './information'
import menuList from './menuList'
export default {
  name: 'my',
  components:{
    information:information,
    menuList:menuList
  },
  data () {
    return {
			title:''
    }
  },
	mounted:function(){
		this.$emit('changetitle',this.$data.title);
	},
	methods:{
    
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.my{
  margin-top:10vw;
}

</style>



