<template>
  <div class="News">
    <div class="News_m">
      <div class="News-item" v-for="(item,index) in lists" v-if='index < 5' @click.stop='go(item.id)'>
        <span class='isTop'></span>
        <span class="headlines">{{item.title}}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "index",
  data() {
    return {
      src: "public/quanzi/tieba/top/top.png",
      //src: "public/images/horn.png",
      lists: [
      ],
    };
  },
  created(){
    this.getkey();
  },
  methods: {
    getTopPost(key){
      this.axios({
        url:'topPost',
        method:'get',
        params:{
          key:key
        }
      }).then(res => {
        console.log(res);
        this.lists = res.data.data;
      })
    },
    getkey(){
      this.axios({
        url:'forumApp'
      }).then(res => {
        this.getTopPost(res.data.data);
      })
    },
    go(id){
      this.$router.push({name: 'tieziitem',query:{id:id}});
    }
  }
};
</script>


<style scoped>
/* top */
.News_m {
  margin-bottom: 3vw;
  
}
.News-item{
  background: #fff;
  display: flex;
  align-items: center;
  font-size: 0.9rem;
  border-bottom: 1px solid #ddd;
  width:96vw;
  overflow:hidden;
  height:10vw;
  padding:0vw 2vw;
}

.isTop{
  padding:0.5vw 3vw;
  color:red;
  border:1px solid red;
  border-radius:1vw;
  font-size:3vw;
}
.headlines {
  padding: 2vw;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
/* top */
</style>



