<template>
  <div class="posting">
		<div class='posting-text'><!---->
      <div class='posting-head'>
        <input class='posting-head-title' placeholder="" v-model="valueText"/>
      </div>
      
      <textarea class='posting-content' placeholder="" :rows="6" v-model="content1"></textarea>
		</div>
		<div class='posting-img'><!---->
			<img src='public/quanzi/posting/image.png' @click.stop='chooseimg' class='posting-img-item'>
      <span class='posting-btn' @click.stop='posting'></span>
		</div>
		<div class='addimg-area'>
			<img v-for='(item,index) in images' :src='item' :key='index' class='addimg' @click.stop='lookimg(item)'>
		</div>
    
    <div class='keywords' v-if='keywords.length != 0'>
      <div class='keywords-title'></div>
      <div 
        class='keywords-item' 
        v-for='(item,index) in keywords'
        @click='choose(index)'
        :class='keywordsIndex == index ? "select":""'
      >
        {{item.name}}
      </div>
    </div>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui';
import { Toast } from 'mint-ui';
export default {
  name: 'posting',
  data () {
    return {
			title:'',
			valueText:'',
			content1:'',
			images:[],
			serverId:[],
			address:'',
      select:'',
      keywords:[],
      keywordsIndex:-1,
      img:''
    }
  },
	created(){
		let that = this;
		that.getground();
		that.getkeyWords();
	},
	methods:{
		posting:async function(e){/**/
      console.log(this.content1)
			let that = this;
			let data = [];
			if(this.images.length != 0){
				for(let i in this.images){
					data.push(await this.upimage(this.images[i]));
				}
			}
			if(this.valueText.length ==0 && this.content1.length == 0){
				Toast('');
				return false;
			}
      let params = {
        title:this.valueText,
        keywords_id:this.select,
        content:this.content1,
        type:1,
        address:this.address,
        img:JSON.stringify(data),
        class:1
      }
      Indicator.open('Loading...');
			this.axios({
				url:'posts',
				method:'post',
				data:this.qs.stringify(params)
			}).then(re => {
        Indicator.close();
        if(re.data.status == 200){
          this.$router.push({name: 'tiezi'});
        }else{
          Toast(re.data.message)
        }
			})
		},
		chooseimg:function(e){/**/
			let that = this;
			let nums = 9 - that.images.length;
			if(nums <= 0 ){
				that.$toast.text({
					duration: 1000,
					message: '9'
				});
				return false;
			}
			wx.chooseImage({
				count:nums,
				sizeType: ['original'],
				sourceType: ['album', 'camera'],
				success: function (res) {
					that.images.push(...res.localIds);
				}
			});
			
		},
		lookimg:function(e){/**/
			let that = this;
			wx.previewImage({
				current:e,
				urls: that.images
			})
		},
		upimage:async function(images){/**/
			return await new Promise((resolve,reject) =>{
				wx.uploadImage({
					localId:images,
					isShowProgressTips: 1,
					success:function(res){
						resolve(res.serverId);
					},
					fail:function(res){
					}
				})
			});
		},
		getground:function(){/**/
			let that = this
			wx.getLocation({
				type: 'wgs84',
				success: (res) => {
          this.axios({
            url:'forumUserLatLng',
            params:{
              longitude:res.longitude,
              latitude:res.latitude
            }
          }).then(res => {
            this.address = res.data.data;
          })
				}
			});
		},
    /**
     * 
    */
    getkeyWords(){
      this.axios({
        url:'keywords'
      }).then(res => {
        this.keywords = res.data.data;
      })
    },
    /**
     * 
    */
    choose(index){
      if(index == this.keywordsIndex){
        this.keywordsIndex = -1;
        this.select = '';
        return false;
      }
      this.select = this.keywords[index].id;
      this.keywordsIndex = index;
    }
	},
	mounted:function(){
		this.$emit('changetitle',this.$data.title)
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.posting{
	background:#fff;
  margin-top:10vw;
  
  .posting-text{
    display:flex;
    justify-content:center;
    align-items:center;
    padding:2vw;
    flex-flow:row wrap;
    
    .posting-head{
      flex:1;
      display:flex;
      justify-content:center;
      align-items:center;
      
      .posting-head-select{
        width:20vw;
        appearance: none;
        border: none;
        background:none;
        direction: rtl;
        
      }
      
      .posting-head-title{
        flex:1;
        border:none;
        outline:none;
        padding:2vw;
        border-bottom:1px solid #eee;
      }
      
    }
    
    .posting-content{
      width:100%;
      border:none;
      outline:none;
      padding:2vw;
      border-bottom:1px solid #eee;
      resize:none;
    }
    
  }
  .posting-img{
    display:flex;
    flex-flow:row nowrap;
    justify-content:space-between;
    align-items:center;
    margin-top:1vw;
    .posting-img-item{
      width:6vw;
      height:6vw;
      margin-left:3vw;
    }
    .posting-btn{
      color:#555;
      border:1px solid #555;
      font-size:3.5vw;
      padding:1vw 4vw;
      border-radius:2vw;
      margin-right:2vw;
    }
  }
  
    
  
}


.weui-btn{
	left: 84vw;
}
.weui-btn_primary{
	background:#19b5ff;
}
.addimg-area{
	margin-top:4vw;
	margin-left:2vw;
	display:flex;
	flex-direction:row;
	flex-wrap:wrap;
  border-bottom:1px solid #eee;
}
.addimg{
	width:30vw;
	height:30vw;
	margin-right:1vw;
	margin-bottom:1vw;
  
}
.keywords{
  display:flex;
  justify-content:flex-start;
  align-items:center;
  flex-flow:row wrap;
  padding:2vw 2vw;
  
  .keywords-title{
    width:100%;
    margin-bottom:2vw;
  }
  
  .keywords-item{
    padding:0.5vw 2.5vw;
    border-radius:1vw;
    font-size:4vw;
    color:#999;
    border:1px solid #999;
    margin:1vw;
  }
  .select{
    color:#19b5ff;
    border:1px solid #19b5ff;
  }
}

</style>



