<template>
  <div class="tieziItemHeader" v-if='items.length != 0'>
		<div class='lz'>
			<div class='lz-title'>
        <span v-if='items.is_essence == 1' class='lz-title-jing'></span>
        <span>{{items.title}}</span>
      </div><!---->
			<div class='lz-card'><!---->
				<img :src='items.avatar' class='lz-card-img'>
				<div class='lz-card-item'>
					<div>{{items.nickname}} <span :class='items.sex == 1 ? "lz-card-man":"lz-card-woman"'>{{items.sex == 1 ? "":""}}</span></div>
					<div class='lz-card-time'>{{items.create_time}}</div>
				</div>
			</div>
			<div class='lz-content'><!---->
				<p class='lz-content-text' v-html='items.content'></p>
				<div class='lz-content-img'>
					<img v-for='(src,index) in items.pic_urls' :src='src' @click.stop='lookimg(index,items.pic_urls)' :key='index' class='lz-content-img-item'>
				</div>
			</div>
			
      <div class='lz-like_count'>
        <div class='lz-like_count-item'>
          <img class='lz-like_count-img' src='/public/quanzi/tieba/data/unpraise.png'>
          <div class='lz-like_count-nums'>{{items.like_count}}</div>
        </div>
      </div>
		</div>
  </div>
</template>

<script>
export default {
  name: 'tieziItemHeader',
  props:{
    items:Object
  },
  data () {
    return {
			
    }
  },
  filters:{
    content(val){
      console.log()
      return val.replace(/\n/g,"\r");
    }
  },
	methods:{
		lookimg (index,data) {
      wx.previewImage({
        current: data[index], // http
        urls: data// http
      });
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.lz{
  padding: 2vw 2vw 0 2vw;
  background:#f8f8f8;
  .lz-title{
    font-weight:500;
    font-size:5vw;
    .lz-title-jing{
      color:red;
      border:1px solid red;
      border-radius:1vw ;
      font-size:3vw;
      padding:0.5vw 3vw;
    }
  }
  .lz-card{
    display:flex;
    align-items:center;
    margin:1vw 0vw;
    .lz-card-img{
      border-radius:50%;
      width:12vw;
      height:12vw;
      margin-right:1vw;
    }
    .lz-card-item{
      font-size:5vw;
      .lz-card-time{
        font-size: 3.5vw;
      }
    }
  }
  
  .lz-content{
    .lz-content-text{
      width:100%;
      white-space: pre-wrap;
      font-size:16px;
    }
    .lz-content-img{
      display:flex;
      align-items:center;
      flex-wrap:wrap;
      margin-top:1vw;
      .lz-content-img-item{
        width:30vw;
        height:30vw;
        margin-right: 2vw;
        margin-bottom: 2vw;
      }
    }
  }
  .lz-like_count{
    display:flex;
    justify-content:center;
    align-items:center;
    .lz-like_count-item{
      display:flex;
      justify-content:center;
      align-items:center;
      border:1px solid #ddd;
      padding:1vw 10vw;
      border-radius:5vw;
      margin-top:1vw;
      margin-bottom:2vw;
      .lz-like_count-img{
        width:5vw;
        height:5vw;
      }
      .lz-like_count-nums{
        margin-left:1vw;
      }
    }
  }
}
.lz-card-man{
  color:#19b5ff;
}
.lz-card-woman{
  color:#ffb0d2;
}
</style>



