<template>
  <div class="header" v-bind:style='{ background : imgurl , "background-size": "100% 100%"}'>
    <!--   -->
    <div class="center-msg" >
      <div class="searchs" @click.stop='go'>
        <img class='search-img' src='/public/quanzi/tieba/header/search.png'/>
        <div class="search-input"></div>
      </div>
      <div class="mask">
        <img class='mask-img' :src="logo">
        <div class="user_information">
          <p class="bubble">{{name}}</p>
          <span class='tiezi-information-nums'>{{look}}</span>
          <span class='tiezi-information-nums'>{{users}}</span>
        </div>
      </div>
    </div>
    
    
    <!--  -->
    <!-- 
    <div class="que-operate flex-wrp">
      <div class="invite flex-item">
        <img src="public/quanzi/Sign_in.png" alt>
        <span></span>
      </div>
      <div class="write flex-item">
        <img src="public/quanzi/invitation.png" alt>
        <span></span>
      </div>
    </div>
    -->
    
  
  </div>
</template>

<script>
export default {
  name: "index",
  data() {
    return {
      name:'',
      look:'',
      users:'',
      logo:'',
      title: "",
      search: [],
      imgurl:'url(public/quanzi/bg.png)'
    };
  },
  mounted: function() {},
  created(){
    this.getdata();
  },
  methods: {
    getdata(){
      this.axios({
        url:'forumAppInfo'
      }).then(res => {
        console.log(res.data)
        this.name = res.data.data.name;
        this.look = res.data.data.hits_count;
        this.users = res.data.data.number;
        this.logo = res.data.data.pic_url;
      })
    },
    go(){
      this.$router.push({name:'search'})
    }
  }
};
</script>


<style scoped>
.hello {
  background: #e6e1e1;
}
/*  */
.center-msg {
  height: 37vw;
  padding-top: 3vw;
  background: rgba(0,0,0,0.3);
}
.searchs{
  border-radius: 4px;
  position: relative;
  width: 90vw;
  left:5vw;
  display:flex;
  align-items:center;
}
.search-img{
  width:5vw;
  height:5vw;
  position:absolute;
  left:1vw;
}
.search-input{
  width: 90vw;
  padding: 7px 0 7px 24px;
  border-radius: 4px;
  background:#fff;
  font-size:3vw;
}
.iconfont {
  position: absolute;
  top: 11px;
  left: 30px;
  z-index: 1;
}
input {
  outline: none;
  border: 0;
}
/* */
.head_p {
  position: relative;
}
.hiddenInput {
  position: absolute;
  top: 8vw;
  left: 8vw;
  opacity: 0;
  width: 20vw;
  height: 20vw;
}
.head_img img { 
  margin: 8vw;
  width: 20vw;
  height: 20vw;
}
.mask {
  display: flex;
  width:100%;
  justify-content: flex-start;
  align-items: flex-end;
  margin-top:5vw;
}
.mask-img{
  width:20vw;
  height:20vw;
  margin-left:7vw;
  border:1px solid #fff;
  box-sizing:border-box;
}
.user_information {
  flex:1;
  color: #fff;
  text-align: right;
  margin-right:7vw;
}

.tiezi-information-nums{
  font-size:3vw;
  margin-left:2vw;
}

.bubble {
  font-size: 1.2rem;
}

/*   */
.que-operate {
  width: 100%;
  border-top: 1px solid #ebebeb;
  border-bottom: 1px solid #ebebeb;
  color: #889091;
  vertical-align: middle;
  background: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 3vw;
}
.que-operate .flex-item {
  padding: 3vw 0;
  font-size: 0.6rem;
}
.que-operate .invite {
  width: 50%;
  border-right: 2px solid #ebebeb;
  text-align: center;
}
.que-operate .write {
  width: 50%;
  /* text-align: center; */
  display: flex;
  justify-content: center;
  align-items: center;
}
.que-operate img {
  width: 5vw;
  height: 5vw;
  vertical-align: middle;
  margin: 0 1vw 0 0;
}
/*  */
/*  */
.classification {
  background: #fff;
  display: flex;
  margin-bottom: 3vw;
}
.classificate {
  width: 25%;
  text-align: center;
  padding: 3vw 0;
}
.classification img {
  width: 8vw;
  height: 8vw;
}
.houses {
  font-size: 0.8rem;
  color: #858585;
}
/*  */
/*   */
.horns {
  background: #fff;
  padding: 1vw 0 1vw 3vw;
  display: flex;
  align-items: center;
  margin-bottom: 3vw;
  font-size: 0.9rem;
}
.horns img {
  width: 8vw;
  height: 8vw;
}
/*   */
/* top */
.News_m {
  margin-bottom: 3vw;
}
.News {
  background: #fff;
  display: flex;
  align-items: center;
  font-size: 0.9rem;

  border-bottom: 1px solid #ddd;
  height: 12vw;
}
.News img {
  width: 8vw;
  height: 8vw;
}
.News .headlines {
  padding: 2vw;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
/* top */
</style>



