<template>
  <div class="search">
		<Search @getdata='getData'></Search>
    <Data :data='searchList'></Data>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui';
import { Toast } from 'mint-ui';
import Data from './data';
import Search from './searchInput';
export default {
  name: 'search',
  components:{
    Data:Data,
    Search:Search,
  },
  data () {
    return {
			searchList:'',
      searchData:''
    }
  },
	created:async function(){
    this.$emit('changetitle','')
	},
  beforeDestroy(){
    window.removeEventListener('scroll', this.handleScroll);
  },
	methods:{
    getData (e) {
      this.searchData = e.str
      let data = {
        str:e.str,
        page:1
      }
      this.axios({
        url:'posts',
        method:'get',
        params:data
      }).then(res => {
        if(res.data.status == 200){
          this.searchList = res.data.data;
        }else{
          Toast('')
        }
        
      })
    },
    getmore () {
      this.axios({
        url:'posts',
        method:'get',
        params:data
      }).then(res => {
        if(res.data.status == 200){
          this.searchList.push(res.data.data);
        }else{
          Toast('')
        }
        
      })
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.search{
	margin-top:10vw;
}
</style>



