<template>
  <div class="message">
    <!--   -->
    <div class="horns">
      <img src="public/quanzi/horn.png" alt>
      <span></span>
      <span> </span>
    </div>
  
  </div>
</template>

<script>
const delay = (function() {
  let timer = 0;
  return function(callback, ms) {
    clearTimeout(timer);
    timer = setTimeout(callback, ms);
  };
})();
export default {
  name: "message",
  data() {
    return {
      
    };
  },
  watch: {
    title() {
      delay(() => {
        this.fetchDate();
      }, 300);
    }
  },
  methods: {
    
  }
};
</script>


<style scoped>
/*   */
.horns {
  background: #fff;
  padding: 1vw 0 1vw 3vw;
  display: flex;
  align-items: center;
  margin-bottom: 3vw;
  font-size: 0.9rem;
}
.horns img {
  width: 8vw;
  height: 8vw;
}
/*   */
</style>



